.submenu-block {
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 5px 5px 5px 0 rgba(128, 128, 128, 0.5);
  margin-bottom: 40px;
  padding: $n20 $n40;

  h1,
  h2 {
    color: $grey-60;
    font-family: $roboto;
    margin-bottom: 16px;
  }

  > ul {
    display: flex;
    flex-wrap: wrap;

    > li {
      flex-basis: 100%;
      padding: 0;
    }
  }

  a {
    color: $menu-blue;
    text-decoration: none;
  }

  .submenu-heading {
    font-weight: bold;
    font-size: 1.2em;
    color: $grey-60;
    margin-top: 18px;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;

    ul {
      margin: 16px;
    }
  }

  li {
    margin: 6px 0 0;

    &.list-heading > ul {
      margin-left: 0;
    }

    &.list-style-none {
      list-style: none;
    }

    &.list-style-bullet::before {
      content: "";
      background-image: url("images/icons/angle-right.svg");
      background-position: 50% 50%;
      background-size: 1rem 1rem;
      background-repeat: no-repeat;
      margin-right: 2px;
      height: 1rem;
      width: 1rem;
      margin-bottom: -2px;
      display: inline-block;
      @supports (mask-repeat: no-repeat) or (-webkit-mask-repeat: no-repeat) {
        & {
          background-image: none;
          background-color: $menu-blue;
          -webkit-mask-image: url("images/icons/angle-right.svg");
          mask-image: url("images/icons/angle-right.svg");
          mask-repeat: no-repeat;
          -webkit-mask-repeat: no-repeat;
          margin-right: 0;
        }
      }
    }

    &.list-style-number {
      list-style: decimal;
      margin-left: 20px;
    }
  }

  &.submenu-long > ul > li {
    @include large-and-up {
      flex-basis: 50%;
    }
  }

  &.submenu-short > ul > li {
    @include large-and-up {
      flex-basis: 33%;
    }
  }

  &.submenu-sidebar {
    z-index: 4;

    @include medium-and-up {
      float: right;
      max-width: 350px;
      margin: 0 0 $n15 $n15;
    }
  }
}

.back-top,
div.page-template > .back-top {
  background-image: url("images/arrow-up.svg");
  background-repeat: no-repeat;
  position: fixed;
  height: 44px;
  width: 44px;
  left: calc(85% - 26px);
  bottom: 50px;
  background-color: #074365;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .25);
  display: none;
  z-index: 1000;

  @include x-large-and-up {
    height: 60px;
    width: 60px;
  }

  &:hover {
    text-decoration: none;
  }
}
